import axios from 'axios'
import router from '@/router'
import { BASE_URL, CODE } from '@/utils'

const Axios = axios.create({
  baseURL: BASE_URL,
  ValidityState: () => {
    return status < 500
  }
})

// 
Axios.interceptors.request.use((config) => {
  config.headers['token'] = sessionStorage.getItem('token') // token
  return config
}, (error) => {
  return Promise.reject(error)
})

// 
Axios.interceptors.response.use((response) => {
  if (response.status === CODE.SUCCESS) { // 
    return response
  } else if (response.status === CODE.NO_PERMISSION) { // 
    return router.push({ path: 'prompt' })
  } else if (response.status === CODE.NOT_FIND) { // ""
    return router.push({ path: 'notFind' })
  }
}, (error) => {
  return Promise.reject(error)
})

export default Axios



