<template>
  <div class="topRanking">
    <!--  -->
    <van-row class="sub-header">
      <!-- () -->
      <van-col class="text-left" span="18">
        <span
        v-for="(item, index) of tabList"
        :key="index"
        :class="active === index ? 'btn-left btn-active' : 'btn-left'"
        @click="activeTab(item, index)">
        {{ item.label }}
        </span>
      </van-col>
      <!--  -->
      <van-col class="text-right" span="6">
        <span class="btn-right" @click="handleRightBtn">{{ showPart.rightBtn }}</span>
      </van-col>
    </van-row>
    <van-row style="height: 5px;"></van-row>
    <!--  -->
    <title-info :title="showPart.title" :remark="showPart.remark"></title-info>
    <!--  -->
    <el-table
    :data="showPart.tableData"
    :height="height"
    :empty-text="PROMPT.EMPTY_TEXT"
    header-cell-class-name="table-header"
    cell-class-name="table-cell"
    style="width: 100%"
    stripe>
      <el-table-column
      prop="album_title"
      label="1"
      min-width=""
      :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column
      prop="author"
      label="2"
      width=""
      :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column
      prop="title"
      label="3"
      width=""
      :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column
      prop="rank_change"
      label="4"
      width=""
      :show-overflow-tooltip="true">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { NON_DYNAMIC_HEIGHT, PROMPT } from '@/utils'
import TitleInfo from '@/components/BaseTitleInfo'
import { testData } from '@/api'

export default {
  name: 'TopRanking',
  components: { TitleInfo },
  data () {
    return {
      PROMPT, // 
      showPart: { // 
        label: '',
        title: '',
        remark: [],
        tableData: [],
        rightBtn: ''
      },
      active: 0, // 
      freshTime: '', // -
      tabList: [{
        label: '', // tab
        title: '11()', // 
        remark: [''], // 
        valInx: 0, // -1
        tableData: [],
        rightBtn: ''
      }, {
        label: '2',
        title: '22()',
        remark: [''],
        valInx: 0,
        tableData: [],
        rightBtn: ''
      }]
    }
  },
  computed: {
    height () { //  =  -  - 
      return NON_DYNAMIC_HEIGHT - 18 * this.showPart.remark.length - 5
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      this.showPart = this.tabList[this.active]
      this.showPart.remark[1] = ' ' + new Date().toLocaleString()
      this.getDataInfo(this.showPart, 0)
    },
    getDataInfo (obj, index) {
      testData().then(response => {
        obj.title = response.data.result[index].name
        obj.remark[0] = response.data.result[index].color
        obj.tableData = response.data.result[index].content
        obj.rightBtn = response.data.result[index].bg_color
        if (JSON.stringify(obj) !== JSON.stringify(this.showPart)) {
          this.showPart = obj
        }
      })
    },
    handleRightBtn () { // 
      alert(this.showPart.rightBtn)
    },
    activeTab (item, index) { // tab
      this.freshTime = Math.random()
      this.active = index // 
      this.showPart.remark[item.valInx] = ' ' + this.freshTime // 
      this.getDataInfo(this.tabList[index], index)
    }
  }
}
</script>

<style>
</style>



